

















































































































import UploadImage from './UploadImage.vue'
import clip from '@/utils/clipboardWeb.ts' // use clipboard directly
import {Component,Vue,Prop,Mixins} from 'vue-property-decorator'
import {FormatMixin} from '@/assets/mixin'
@Component({
  components:{UploadImage}
})
export default class ViewComponent extends Mixins(FormatMixin) {
  /* prop */
  @Prop() prop!: any; // 父组件传值
  /**data */
  ruleForm = {}
  /**computed */
  computedUploadImage:any = {
    direct: this.prop.direct,
    disabled: false,
  }
  get propUploadImage() {
    return this.propUploadImage;
  }
  set propUploadImage(file:any) {
    this.computedUploadImage.file = file
  }

  /**lifecycle hook */
  created(){
    this.ruleForm = this.prop
    // console.log('-----',this.ruleForm)
  }

  /**data */
  // 辅助静态展示信息
  optionProject = [
    {
      key: 'common',
      role: '通用',
    },
    {
      key: 'msf',
      role: '小微社交金融',
    },
    {
      key: "dkd",
      role: "道口贷",
    },
    {
      key: "dky",
      role: "道口云",
    },
  ]
  optionRole = [
    {
      key: 'admin',
      role: '管理员',
      des: 'Super Administrator. Have access to view all pages.',
    },
    {
      key: "editor",
      role: "编辑",
      des: "Normal Editor. Can see all pages except permission page"
    },
    {
      key: "visitor",
      role: "游客",
      des: "Just a visitor. Can only see the home page and the document page"
    },
  ]
  

  /* method */
  handleDelete(index:number,row:any){
    if(!confirm('确实要删除吗?')) return false;
    const url = row.url;
    // const nameIndex = url.lastIndexOf('/file/');
    const nameIndex = url.lastIndexOf('/dkd-front/msf/file/');
    const name = url.slice(nameIndex+1)
    this.emit({ // 发射子组件参数
      /* 请求返回的数据 */
      data:{
        name,
      },
      /* 其他控制字段 */
      method:'delete',
      index,
      id:row._id,
    })
  }
  handleEdit(index:number,row:any){
    this.emit({ // 发射子组件参数
      /* 请求返回的数据 */
      /* 其他控制字段 */
      method:'patch',
      title: '编辑',
      index,
      ruleForm:row,
    })
  }
  handleEditInline(index:number,row:any){
    // 编辑
    row.edit = true
  }
  handleCopy(str: any, event: any) {
    clip(str, event)
  }
  changeSelect(row:any){
    row.des = (this.getRoleObj(row.role) as any).des
  }
  getRoleFromObj(role:string){
    let roleObj = {}
    this.optionProject.some((item:any)=>{
      if(item.key == role){
        roleObj = item;
        return true;
      }
    })
    return roleObj
  }
  getRoleObj(role:string){
    let roleObj = {}
    this.optionRole.some((item:any)=>{
      if(item.key == role){
        roleObj = item;
        return true;
      }
    })
    return roleObj
  }
  handleEditInlineSave(index:number,row:any){
    (this.$refs['ruleForm'] as any).validate((valid: boolean) => {
      if (valid) {
        row.edit = false
        this.emit({ // 发射子组件参数
          /* 请求返回的数据 */
          data: row,
          /* 其他控制字段 */
          method:'patch',
          mode:'editInline',
          index,
        })
      }
    })
  }
  /* 向父组件发射值 */
  emit(response: object) {
    this.$emit(
      'emit',
      response // 发射子组件参数
    )
  }
}
